<template>
  <div class="page">
    <!-- <article>
      <p>By the time you're reading this, The M Library provides you with :</p>
      <ul>
        <li>
          <a href="#theming">
            Automatic Dark &amp; Light theming based on your colors.
          </a>
        </li>
        <li>
          <a href="#variables">
            Variables for colors, fonts and UI variables.
          </a>
        </li>
        <li><a href="#functions"> Color editing functions. </a></li>
        <li><a href="#animations"> Vue fade &amp; slide animations. </a></li>
      </ul>
    </article> -->
    <article id="theming">
      <h3>Theming</h3>
      <p>
        m-labs makes multiple themes feature for you. All you have to do
        is to choose the colors for the themes you want then you can toggle the <m-code code="m-themeName"/> to your body tag.
        <br>
        In your app component import the <m-code code="setDefaultTheme({main, text, accent, onAccent})"/>, <m-code code="setTheme(<theme-name>, {main, text, accent, onAccent})"/> and <m-code code="configUI({arFont, enFont}, <ui-radius>px)"/> functions and call them in the <m-code code="mounted"/> hook for example.
        <br>
        <m-light text="Note :"/> the color names are not changable you'll have to use these specific color names in order for the M components to work.
      </p>
      <m-code
        type="block"
        language="Vue"
        code="
mounted() {

  // setup your themes

  setDefaultTheme({
    main: 'rgba(255, 255, 255, 1)',
    text: 'rgba(0, 0, 0, 1)',
    accent: 'rgb(82, 0, 224)',
    onAccent: 'rgba(255, 255, 255, 1)',
  });

  setTheme('dark', {
    main: 'rgba(0, 0, 0, 1)',
    text: 'rgba(255, 255, 255, 1)',
    accent: 'rgba(82, 0, 224, 1)',
    onAccent: 'rgba(255, 255, 255, 1)',
  });

  configUI({en: 'Cairo', ar: 'Poppins'}, '6px');

  // change one of your theme colors :

  setTheme('dark', {main: 'rgba(0, 0, 0, 1)'});
}
"
      />
    </article>
    <article id="variables">
      <h3>M Variables</h3>
      <p>
        The library converts your colors to both css &amp; sass variables, for
        example the accent color shown in the object above can be accessed with
        <m-code code="$color_accent" /> and <m-code code="var(--m-color-accent)" />. if
        you choose to add more colors you'll have to register your sass
        variables manually while your css variables are created automatically.
        <br />
        beside color variables, the there are more variables for UI, fonts and
        animations.
      </p>
    </article>
    <article id="functions">
      <h3>Custom Functions</h3>
      <p>Shows the power of the theme engine and the hsla colors.</p>
      <ul>
        <li>
          <h4>darker("color-name", amount) :</h4>
          <p>
            darkens the color with color-name by the percent amount (positive
            only).
          </p>
          <m-code
            type="block"
            language="Sass"
            code="
button {
  background: $color_accent;
  &:hover {
    background: darker('accent', 20%);
    transform: scale(1.04);
  }
}
"
          />
        </li>
        <li>
          <h4>lighter("color-name", amount) :</h4>
          <p>
            lightens the color with color-name by the percent amount (positive
            only).
          </p>
          <m-code
            type="block"
            language="Sass"
            code="
button {
  background: $color_accent;
  &:hover {
    background: lighter('accent', 20%);
    transform: scale(1.04);
  }
}
"
          />
        </li>
        <li>
          <h4>changeOpacity("color-name", amount) :</h4>
          <p>
            darkens the color with color-name by the fraction amount (positive
            or negative).
          </p>
          <m-code
            type="block"
            language="Sass"
            code="
.tab {
  background-color: changeOpacity('main', -.4);
}
"
          />
        </li>
      </ul>
    </article>
    <article id="animations">
      <h3>Vue animations <m-light text="alpha"/></h3>
      <p>
        Still in beta state, this feature provides classes for Vue transitions.
        It uses the animation variables in <m-code code="_configs.scss" /> like
        duration and directions.
        <br>
        <ul>
          <li>
            use <m-code code="fade-'direction'"/> class like <m-code code="fade-left"/> to make your component fade from &amp; to left.
          </li>
          <li>
            use <m-code code="slide-'direction'"/> class like <m-code code="slide-left"/> to make your component slide from &amp; to left.
          </li>
        </ul>
      </p>
    </article>
  </div>
</template>

<script>
export default {
  name: "Sass",
};
</script>

<style lang="scss" scoped></style>
